import * as Yup from "yup";

// Validaciones realizadas para el formulario con yup, tanto de campos obligatorios como min y max de caracteres:
const mySchema = Yup.object().shape({
    name: Yup.string()
    .min(6, "El nombre debe tener 5 caracteres como minimo")
    .max(12, "El mximo de caracteres es de 12")
    .required("Por favor, ingrese un nombre"),

    surname: Yup.string()
    .min(6, "El apellido debe tener 5 caracteres como minimo")
    .max(12, "El mximo de caracteres es de 12")
    .required("Por favor, ingrese un apellido"),

    email: Yup.string()
    .email("El email no es valido")
    .required("Por favor, ingrese un email"),

    dni: Yup.string()
    .matches(/^\d+$/, "Ingrese un DNI vlido")
    .min(5, "El nmero es muy pequeo")
    .max(14, "El nmero es muy extenso")
    .required("Por favor, ingrese su nmero DNI"),

    address: Yup.string()
    .required("Por favor, ingrese una direccin"),

    phone: Yup.string()
    .matches(/^\d+$/, "Ingrese un nmero vlido")
    .min(5, "El nmero es muy pequeo")
    .max(14, "El nmero es muy extenso")
    .required("Por favor, ingrese un nmero"),

    picfile: Yup.mixed()
    .required('Debes adjuntar tu foto'),

    idfile1: Yup.mixed()
    .required('Debes adjuntar el frente de tu identificacin'),

    idfile2: Yup.mixed()
    .required('Debes adjuntar el dorso de tu identificacin')
  })

export default mySchema;